import React, { useEffect, useState } from "react";
import {
	Box,
	Button,
	Container,
	CircularProgress,
	Snackbar,
	TextField,
	Typography,
	makeStyles,
} from "@material-ui/core";
import VisibilityIcon from "@mui/icons-material/Visibility";
import VisibilityOffIcon from "@mui/icons-material/VisibilityOff";
import AuthContainer from "../../../containers/authContainer";
import { NavLink, useHistory } from "react-router-dom";
import { useFormik } from "formik";
import { Alert } from "@mui/material";
import { useDispatch, useSelector } from "react-redux";
import { postLoginData, setAnswerLogin } from "../../../redux/reducers/login";
import cookie from "cookie_js";
import { istance, istanceSecond } from "../../../api";
import { getProfileData } from "../../../redux/reducers/myProfile";
import F2aDialog from "../../../components/2faDialog";
import { useLocation } from "react-router-dom";
import { isAuthenticated } from "../../../utils/isAuthenticated";

if (isAuthenticated()) document.location.href = "/"

const Login = ({ t }) => {
	const dispatch = useDispatch();
	const history = useHistory();
	const location = useLocation();
	const domain = window.location.hostname;
	const [eyes, setEyes] = useState(true);
	const [open, setOpen] = useState(false);
	const { answerLoginAlert } = useSelector(({ loginReducer }) => loginReducer);
	const [reload, setReload] = useState(false);
	const formik = useFormik({
		initialValues: {
			email: "",
			password: "",
		},
		onSubmit: (values) => {
			dispatch(postLoginData(values));
			setReload(true);
			cookie.set("ActivationAccountEmail", values.email, { path: "/" });
		},
	});

	useEffect(() => {
		if (answerLoginAlert.message?.security) setOpen(true);
		if (
			answerLoginAlert.message?.messages === "Ваш аккаунт еще не активирован!"
		) {
			setTimeout(() => {
				history.push("/accounts/activation");
			}, 1000);
		}
		if (answerLoginAlert.severity === 200) {
			setTimeout(() => {
				let expires = new Date(
					answerLoginAlert.message.expires * 1000,
				).toUTCString();

				document.cookie =
					"token=" +
					answerLoginAlert.message.token +
					"; domain=" +
					domain +
					"" +
					"; expires=" +
					expires +
					";path=/";
				istance.defaults.headers.Authorization = `Bearer ${answerLoginAlert.message.token}`;
				istanceSecond.defaults.headers.Authorization = `Bearer ${answerLoginAlert.message.token}`;
				dispatch(getProfileData());
				cookie.removeSpecific("ActivationAccountEmail", {
					path: "/",
					domain: domain,
				});
				dispatch(setAnswerLogin({ ...alert, open: false }));
				location.search
					? (window.location.href = location.search.split("=")[1])
					: history.push("/dashboard/main");
			}, 700);
		}

		setReload(false);
		// eslint-disable-next-line
	}, [answerLoginAlert.severity]);

	useEffect(() => {
		if (answerLoginAlert.message?.messages === "Вы уже авторизованы!") {
			cookie.removeSpecific("token", {
				path: "/",
				domain: domain,
			});
			setTimeout(() => {
				window.location.reload();
			}, 200);
		}
	}, [answerLoginAlert.message]);

	const classes = useStyles();

	return (
		<section className="dark" style={{ minHeight: "100vh" }}>
			<Snackbar
				open={answerLoginAlert.open}
				autoHideDuration={1000}
				anchorOrigin={{ vertical: "top", horizontal: "center" }}
				onClose={() =>
					dispatch(
						setAnswerLogin({
							...alert,
							open: false,
							message: answerLoginAlert.message,
						}),
					)
				}
			>
				<Alert
					onClose={() => dispatch(setAnswerLogin({ ...alert, open: false }))}
					severity={answerLoginAlert.severity === 200 ? "success" : "error"}
					variant="filled"
				>
					{answerLoginAlert.severity === 200
						? t("SuccessfulLogin")
						: answerLoginAlert.message && answerLoginAlert.message.messages}
				</Alert>
			</Snackbar>
			<Container maxWidth="lg">
				<AuthContainer user>
					<Typography variant="h3" gutterBottom component="div">
						{t("LogInTitle")}
					</Typography>
					<Typography
						style={{ fontSize: 20 }}
						variant="subtitle1"
						gutterBottom
						component="div"
					>
						{t("LogInDontAccount")}{" "}
						<NavLink to="/accounts/register" className="link">
							{t("SignUp")}
						</NavLink>
					</Typography>
					<form onSubmit={formik.handleSubmit} autoComplete="off">
						<span className="label">E-mail</span>
						<TextField
							label={t("LabelEmail")}
							type="email"
							className={classes.formControl}
							variant="outlined"
							name="email"
							value={formik.values.email}
							onChange={formik.handleChange}
							required
						/>
						<span className="label">{t("Password")}</span>
						<Box style={{ position: "relative" }}>
							<TextField
								type={eyes ? "password" : "text"}
								id="outlined-basic"
								className={classes.formControlPassword}
								label={t("LabelPasswordYou")}
								variant="outlined"
								name="password"
								value={formik.values.password}
								onChange={formik.handleChange}
								required
							/>

							<span onClick={() => setEyes(!eyes)} className="form__eyes">
								{eyes ? <VisibilityOffIcon /> : <VisibilityIcon />}
							</span>
						</Box>
						<NavLink
							style={{
								textAlign: "right",
								marginTop: 16,
								fontSize: 14,
								display: "block",
							}}
							to="/accounts/reset"
							className="link"
						>
							{t("ForgotPassword")}
						</NavLink>
						<Button
							style={{ marginTop: 24, height: 56, width: "100%" }}
							color="primary"
							variant="contained"
							size="large"
							type="submit"
						>
							{reload ? (
								<CircularProgress color="secondary" disableShrink />
							) : (
								t("SignInTo")
							)}
						</Button>
					</form>
				</AuthContainer>
			</Container>
			<F2aDialog
				t={t}
				open={open}
				login
				setOpen={setOpen}
				values={formik.values}
			/>
		</section>
	);
};

export default Login;

const useStyles = makeStyles((theme) => ({
	formControlPassword: {
		"& .MuiOutlinedInput-input": {
			background: "transparent !important",
			color: "#fff",
			boxShadow: "none!important",
			paddingRight: "50px",
		},

		"& .MuiInputLabel-formControl": {
			color: "#B8C0CC !important",
			fontWeight: 300,
			whiteSpace: "nowrap",
			textOverflow: "ellipsis",
			overflow: "hidden",
			maxWidth: "90%",
			fontSize: 16,
		},
		"& .MuiOutlinedInput-notchedOutline": {
			borderColor: "#B8C0CC",
		},
	},
	formControl: {
		"& .MuiOutlinedInput-input": {
			background: "transparent !important",
			color: "#fff",
			boxShadow: "none!important",
		},
		"& .MuiInputLabel-formControl": {
			color: "#B8C0CC !important",
			fontWeight: 300,
			whiteSpace: "nowrap",
			textOverflow: "ellipsis",
			overflow: "hidden",
			maxWidth: "90%",
			fontSize: 16,
		},
		"& .MuiOutlinedInput-notchedOutline": {
			borderColor: "#B8C0CC",
		},
	},
}));
