import React, { useLayoutEffect, useState } from "react";
import {
  Button,
  CircularProgress,
  Dialog,
  DialogContent,
  IconButton,
  makeStyles,
  TextField,
  Typography,
} from "@material-ui/core";
import { useDispatch, useSelector } from "react-redux";
import close from "../../assets/images/state/close.svg";
import { Form, Formik } from "formik";
import { postLoginData } from "../../redux/reducers/login";
import { MaskCustom } from "../inputMask/unicalMask";
import {
  confirmWithDraw,
  setWithDrawDataConfirm,
} from "../../redux/reducers/withdraw";
import { useHistory } from "react-router";

const F2aDialog = ({ t, open, login, setOpen, values }) => {
  const classes = useStyles();
  const dispatch = useDispatch();
  const history = useHistory();
  const [loading, setLoading] = useState(false);

  const { answerLoginAlert } = useSelector(({ loginReducer }) => loginReducer);

  const { withDrawDataConfirm, withDrawData } = useSelector(
    ({ withdrawReducer }) => withdrawReducer
  );

  const firstUpdate = React.useRef(true);

  useLayoutEffect(() => {
    if (firstUpdate.current) {
      firstUpdate.current = false;
      return;
    }

    if (answerLoginAlert.severity === 200) setLoading(false);
    else setLoading(false);
  }, [answerLoginAlert]);

  useLayoutEffect(() => {
    if (firstUpdate.current) {
      firstUpdate.current = false;
      return;
    }

    if (withDrawDataConfirm.severity === 500) {
      setLoading(false);
      setOpen(false);
      setTimeout(() => {
        history.push("/dashboard/history");
        dispatch(
          setWithDrawDataConfirm({
            ...withDrawDataConfirm,
            severity: null,
            open: false,
          })
        );
      }, 1000);
    } else {
      setLoading(false);
    }
    // eslint-disable-next-line
  }, [withDrawDataConfirm]);

  return (
    <Dialog
      open={open}
      classes={{ paper: classes.paper }}
      onClose={() => setOpen(false)}
      scroll="body"
    >
      <DialogContent className={classes.dialog}>
        <IconButton
          aria-label="close"
          onClick={() => setOpen(false)}
          className={classes.close}
        >
          <img src={close} alt="" />
        </IconButton>
        <Typography variant="h4" className={classes.title}>
          {t("f2a")}
        </Typography>
        <Typography variant="body1" className={classes.subtitle}>
          {t("f2aSubtitle")}
        </Typography>
        <Formik
          initialValues={
            login
              ? {
                  email: values.email,
                  password: values.password,
                }
              : { "2fa_otp": "" }
          }
          onSubmit={(values) => {
            setLoading(true);
            login
              ? dispatch(postLoginData(values))
              : dispatch(
                  confirmWithDraw(withDrawData?.message?.confirm?.id, values)
                );
          }}
        >
          {({ values, handleChange }) => (
            <Form style={{ width: "100%" }}>
              <TextField
                name="2fa_otp"
                type="number"
                required
                onChange={handleChange}
                value={values["2fa_otp"]}
                id={"000000"}
                className={classes.input}
                label={t("LabelSecurityCode")}
                variant="outlined"
                InputProps={{
                  inputComponent: MaskCustom,
                }}
              />
              <Button
                variant="contained"
                size="large"
                disabled={loading}
                color="primary"
                type="submit"
                className={classes.button}
              >
                {loading ? (
                  <CircularProgress color="secondary" />
                ) : (
                  t("continue")
                )}
              </Button>
            </Form>
          )}
        </Formik>
      </DialogContent>
    </Dialog>
  );
};

export default F2aDialog;

const useStyles = makeStyles((theme) => ({
  paper: {
    width: "100%",
    borderRadius: 16,
    padding: "40px 19px 50px!important",
  },
  dialog: {
    overflow: "hidden",
    display: "flex",
    margin: "0 auto",
    padding: "0!important",
    justifyContent: "center",
    maxWidth: "90%",
    alignItems: "center",
    flexDirection: "column",

    [theme.breakpoints.down(992)]: {
      padding: "16px",
    },
  },
  close: {
    position: "absolute",
    top: 0,
    right: 0,
  },
  title: {
    fontSize: 32,
    lineHeight: 1,
    marginBottom: 10,
    textAlign: "center",
    [theme.breakpoints.down(992)]: {
      fontSize: 28,
    },
  },

  subtitle: {
    color: "#777",
    marginBottom: 40,
    fontWeight: "400",
    textAlign: "center",
  },
  input: {
    background: "#F7F8F9",
    boxShadow: "0px 1px 0px #E2E8F0",
    marginTop: 20,
  },

  button: {
    width: "100%",
    height: 56,
    marginTop: 16,
  },
}));
